.flip-card {
    background-color: transparent;
    width: 700px;
    height: 400px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
    padding: 5px;
    margin: 5px;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    -webkit-transition: -webkit-transform 0.6s;
